
import { mapMutations, mapActions, mapState } from 'vuex'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'


export default {
components: {
Swiper,
SwiperItem,
Flexbox,
FlexboxItem,
Clocker,
XButton,
Tab,
TabItem,
XImg,
Tabbar,
TabbarItem,
Popup,
Confirm,
XInput,
Countdown
},
data () {
return {
swiper: [
'~static/pintuan/banner@2x.png'
],
tabIndex: 0,
showOrderPopup: false,
city: {
cityCode: 0,
cityName: ''
},
carType: '五座轿车',

carInner: false,
buyDiscount: false,
orderPrice: '',
orderInfo: {
title: '五座轿车',
categoryId: [],
orderPrice: 0,
dicountPrice: 0,
salePrice: 0
},
loginDialog: false,
loginLoading: false,
phone: '',
graphCode: '',
verifyCode: '',
graphCodeUrl: '/api/v3/portal/outward/getGraphCode',
disableGraphCode: false,
actEntityId: '',
group: {},
isUserLogin: false
}
},
computed: {
...mapState([
'pintuanProduct'
]),
saleLowPrice: function () {
return this.pintuanProduct.saleLowPrice && (this.pintuanProduct.saleLowPrice / 100)
},
discountLowPrice: function () {
return this.pintuanProduct.discountLowPrice && (this.pintuanProduct.discountLowPrice / 100)
},
cities: function () {
let c = []
this.pintuanProduct.productList && this.pintuanProduct.productList.map(function (el) {
c.push({
cityCode: el.cityCode,
cityName: el.cityName
})
})
return c
}
},
watch: {
},
methods: {
...mapMutations([
'showToast',
'showLoading',
'hideLoading',
'setOrderInfo'
]),
...mapActions([
'sendSmsCode',
'smsLogin',
'getPintuanProduct',
'getPintuanDetails',
'isLogin'
]),
onTabClick: function (index) {
this.tabIndex = index
console.log(index)
},
success: function (e) {
console.log(e)
},
error: function (e) {
console.log(e)
},
orderPopupShow (e) {
console.log(e)
console.log('orderPopupShow')
},
orderPopupHide (e) {
console.log(e)
console.log('orderPopupHide')
},
clickOrderPopupButton (isDiscount) {
this.showOrderPopup = true
this.buyDiscount = isDiscount
this.computeOrderPrice()
},
setCity (city) {
this.city = city
this.computeOrderPrice()
},
setCarType (type) {
this.carType = type
this.computeOrderPrice()
},
setCarInner () {
this.carInner = !this.carInner
this.computeOrderPrice()
},
computeOrderPrice () {
const z = this
let dicountPrice = 0
let salePrice = 0
if (z.city.cityCode !== 0) {
z.pintuanProduct.productList && z.pintuanProduct.productList.map(function (el) {
if (z.city.cityCode === el.cityCode) {
el.categoryList.map(function (p) {
if (p.categoryName === z.carType) {
salePrice = p.salePrice
dicountPrice = p.dicountPrice
z.orderInfo.title = z.carType
z.orderInfo.categoryId = [p.categoryid]
}
if (z.carInner && !p.main) {
salePrice += p.salePrice
dicountPrice += p.dicountPrice
z.orderInfo.title += ' + 内饰清洗'
z.orderInfo.categoryId.push(p.categoryid)
}
})
}
})
}
z.orderInfo.salePrice = salePrice / 100
z.orderInfo.dicountPrice = dicountPrice / 100
z.orderInfo.orderPrice = z.buyDiscount ? z.orderInfo.dicountPrice : z.orderInfo.salePrice
},
submit () {
const z = this
if (z.city.cityCode === 0) {
z.showToast({
type: 'warn',
text: '请选择城市'
})
} else {
let allOrderInfo = Object.assign({}, z.orderInfo, z.city)
if (z.actEntityId) {
allOrderInfo.actEntityId = z.actEntityId
}
sessionStorage.setItem('pintuanOrderInfo', JSON.stringify(allOrderInfo))
z.setOrderInfo()
z.$router.push({
name: 'PintuanOrder',
query: z.$router.query
})
}
console.log('submit')
},
getGraphCode () {
this.graphCodeUrl = '/api/v3/portal/outward/getGraphCode?time=' + Number(new Date())
},
getSmsCode () {
const z = this
if (!(z.phone && z.$refs.phone.valid)) {
z.showToast({
type: 'warn',
text: '请输入正确的手机号'
})
} else if (!(z.graphCode && z.$refs.graphCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入图形验证码'
})
} else {
z.sendSmsCode({
phone: z.phone,
graphCode: z.graphCode
}).then(function (status) {
z.showToast({
type: 'success',
text: '短信验证码已发送'
})
if (status === 200) {
z.disableGraphCode = true
}
})
}
},
doLogin () {
const z = this
if (!(z.phone && z.$refs.phone.valid)) {
z.showToast({
type: 'warn',
text: '请输入正确的手机号'
})
} else if (!(z.graphCode && z.$refs.graphCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入图形验证码'
})
} else if (!(z.verifyCode && z.$refs.verifyCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入短信验证码'
})
} else if (!z.loginLoading) {
let data = {
phone: z.phone,
verificationCode: z.verifyCode
}
z.showLoading({
text: '登陆中……'
})
z.loginLoading = true
z.smsLogin(data).then(function (status) {
z.hideLoading()
if (status === 200) {
z.loginDialog = false
z.loginLoading = false
z.$router.push({
name: 'PintuanMy'
})
}
})
}
},
clickJoin (group) {






this.clickOrderPopupButton(true)
console.log(group)
},
formatDate (time) {
return dateFormat(time, 'YYYY-MM-DD')
},
clickMyBtn () {
if (this.isUserLogin) {
this.$router.push({
name: 'PintuanMy'
})
} else {
this.loginDialog = true
}
}
},
created () {
const z = this
z.actEntityId = z.$route.query && z.$route.query.actEntityId
console.log(z.actEntityId)
z.getPintuanProduct({
activityId: 1
})
if (z.actEntityId) {
z.getPintuanDetails({
actEntityId: z.actEntityId
}).then(function (group) {
z.group = group
})
}
z.isLogin().then(function (status) {
z.isUserLogin = status
})
},
mounted () {
console.log(this)
}
}



